import {Injectable} from '@angular/core';
import { Response } from '@angular/http';
import { AuthHttp } from 'angular2-jwt';
import { MyProfile } from '../models/myprofile';
import { contentHeaders } from '../../common/headers';
import { SharedService } from '../../common/services/shared.service';
import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class MyProfileService {
  constructor(public authHttp:AuthHttp, private  _sharedService: SharedService) {
  }

  public load(): Observable<MyProfile> {
    return this.authHttp.get(this._sharedService.apiUrl + 'my_profile/load')
      .map((data: Response) => <MyProfile> data.json());
  }

  public save(myProfile: MyProfile): Observable<MyProfile> {
    return this.authHttp.post(this._sharedService.apiUrl + 'my_profile/update', myProfile, {headers: contentHeaders})
      .map((response: Response) => <MyProfile>response.json());
  }
}
